import { __ } from '@wordpress/i18n';
import { useState } from 'react';
import { BlockControls, InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, PanelRow, TextControl, RangeControl, CheckboxControl, __experimentalUnitControl as UnitControl, __experimentalNumberControl as NumberControl, Tooltip, Button, Dashicon, ToolbarGroup, ToolbarButton, ToggleControl } from '@wordpress/components';

// Settings Components
import { Label, Background, BDevice, BorderControl, ColorsControl, HelpPanel, InlineMediaUpload, SpaceControl, Typography, MultiShadowControl } from '../../Components';
import { gearIcon } from '../../Components/utils/icons';
import { tabController } from '../../Components/utils/functions';
import { pxUnit, perUnit, emUnit } from '../../Components/utils/options';

import { generalStyleTabs } from './utils/options';

const Settings = ({ attributes, setAttributes, activeIndex, setActiveIndex }) => {
	const { albums, videos, columns, columnGap, rowGap, isPopupWidthAsRatio, filter = { show: true, commonLabel: 'All Videos' }, background, padding, border, shadow, filterBtnTypo, filterBtnColors, filterBtnHoverColors, itemHeight } = attributes;

	const [device, setDevice] = useState('desktop');

	const addVideo = () => {
		setAttributes({
			videos: [...videos, {
				video: '',
				albs: []
			}]
		});
		setActiveIndex(videos.length);
	}

	const updateVideo = (type, val, otherIndex = null) => {
		const newVideos = [...videos];

		if (null !== otherIndex) {
			newVideos[activeIndex][type][otherIndex] = val;
			setAttributes({ videos: newVideos });
		} else {
			newVideos[activeIndex][type] = val;
			setAttributes({ videos: newVideos });
		}
	}

	const duplicateVideo = e => {
		e.preventDefault();

		setAttributes({ videos: [...videos.slice(0, activeIndex), { ...videos[activeIndex] }, ...videos.slice(activeIndex)] });

		setActiveIndex(activeIndex + 1);
	}

	const removeVideo = e => {
		e.preventDefault();

		setAttributes({ videos: [...videos.slice(0, activeIndex), ...videos.slice(activeIndex + 1)] });

		setActiveIndex(0 === activeIndex ? 0 : activeIndex - 1);
	}

	const { video = '', poster = '', caption = '', albs = [] } = videos[activeIndex] || {};

	return <>
		<InspectorControls>
			<div className='vgbInspectorInfo'>
				Need more block like this? Checkout the bundle ➡ <a href='https://wordpress.org/plugins/b-blocks' target='_blank' rel='noopener noreferrer'>B Blocks</a>
			</div>

			<TabPanel className='bPlTabPanel' activeClass='activeTab' tabs={generalStyleTabs} onSelect={tabController}>{tab => <>
				{'general' === tab.name && <>
					<HelpPanel slug='video-gallery-block' docsLink='https://bblockswp.com/docs/video-gallery-block' />


					<PanelBody className='bPlPanelBody addRemoveItems' title={__('Add or Remove Albums', 'video-gallery')}>
						{albums.map((album, index) => <PanelRow key={index}>
							<Label className=''>{__(`Al ${index + 1}:`, 'video-gallery')}</Label>
							<TextControl value={album} onChange={val => {
								const newAlbums = [...albums];
								newAlbums[index] = val;
								setAttributes({ albums: newAlbums });
							}} />

							<Tooltip text={__('Remove this album', 'video-gallery')} placement='top' position='top'>
								<Button className='removeAlbum' onClick={e => {
									e.preventDefault();
									setAttributes({ albums: [...albums.slice(0, index), ...albums.slice(index + 1)] });
								}} ><Dashicon icon='no' /></Button>
							</Tooltip>
						</PanelRow>)}

						<div className='addItem mt15'>
							<Button label={__('Add New Album', 'video-gallery')} onClick={() => setAttributes({ albums: [...albums, ''] })}><Dashicon icon='plus' />{__('Add New Album', 'video-gallery')}</Button>
						</div>
					</PanelBody>


					<PanelBody className='bPlPanelBody addRemoveItems editItem' title={__('Add or Remove Videos', 'video-gallery')}>
						{null !== activeIndex && <>
							<h3 className='bplItemTitle'>{__(`Video No ${activeIndex + 1}:`, 'video-gallery')}</h3>

							<InlineMediaUpload value={video} types={['video']} onChange={val => updateVideo('video', val)} placeholder={__('Video url', 'video-player')} />

							<InlineMediaUpload value={poster} types={['image']} onChange={val => updateVideo('poster', val)} placeholder={__('Poster url', 'video-player')} />

							<PanelRow className='mt10'>
								<Label className=''>{__('Caption:', 'video-gallery')}</Label>
								<TextControl value={caption} onChange={val => updateVideo('caption', val)} />
							</PanelRow>

							<Label>{__('Select Albums:', 'video-gallery')}</Label>
							{albums.map(alb => {
								const isInc = albs.includes(alb);

								return <CheckboxControl label={alb} key={alb} checked={isInc} onChange={val => {
									updateVideo('albs', val ? [...albs, alb] : albs.filter(id => id !== alb));
								}} />;
							})}

							<PanelRow className='itemAction mt20 mb15'>
								<Button className='removeItem' label={__('Remove', 'video-gallery')} onClick={removeVideo}><Dashicon icon='no' />{__('Remove', 'video-gallery')}</Button>

								<Button className='duplicateItem' label={__('Duplicate', 'video-gallery')} onClick={duplicateVideo}>{gearIcon}{__('Duplicate', 'video-gallery')}</Button>
							</PanelRow>
						</>}

						<div className='addItem'>
							<Button label={__('Add New Video', 'video-gallery')} onClick={addVideo}><Dashicon icon='plus' />{__('Add New Video', 'video-gallery')}</Button>
						</div>
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Layout Settings', 'video-gallery')} initialOpen={false}>
						<PanelRow>
							<Label className='mb5'>{__('Columns:', 'video-gallery')}</Label>
							<BDevice device={device} onChange={val => setDevice(val)} />
						</PanelRow>
						<RangeControl value={columns[device]} onChange={val => { setAttributes({ columns: { ...columns, [device]: val } }) }} min={1} max={6} step={1} beforeIcon='grid-view' />

						<NumberControl className='mt20' label={__('Column Gap:', 'video-gallery')} labelPosition='left' value={columnGap} onChange={val => setAttributes({ columnGap: parseInt(val) })} />

						<NumberControl className='mt20' label={__('Row Gap:', 'video-gallery')} labelPosition='left' value={rowGap} onChange={val => setAttributes({ rowGap: parseInt(val) })} />

						<ToggleControl className='mt20' label={__('Popup video width as aspect ratio', 'bplugins')} checked={isPopupWidthAsRatio} onChange={val => setAttributes({ isPopupWidthAsRatio: val })} />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Filter', 'video-gallery')} initialOpen={false}>
						<PanelRow>
							<Label className=''>{__('Common Label', 'video-gallery')}</Label>
							<TextControl value={filter?.commonLabel} onChange={val => setAttributes({ filter: { ...filter, commonLabel: val } })} />
						</PanelRow>
					</PanelBody>
				</>}


				{'style' === tab.name && <>
					<PanelBody className='bPlPanelBody' title={__('Gallery', 'video-gallery')}>
						<Background label={__('Background:', 'video-gallery')} value={background} onChange={val => setAttributes({ background: val })} defaults={{ color: '#fff' }} />

						<SpaceControl className='mt20' label={__('Padding:', 'video-gallery')} value={padding} onChange={val => setAttributes({ padding: val })} defaults={{ vertical: '10px', horizontal: '10px' }} />

						<BorderControl className='mt20' label={__('Border:', 'video-gallery')} value={border} onChange={val => setAttributes({ border: val })} defaults={{ radius: '5px' }} />

						<MultiShadowControl className='mt20' label={__('Shadow:', 'video-gallery')} value={shadow} onChange={val => setAttributes({ shadow: val })} defaults={[{ hOffset: '0px', vOffset: '25px', blur: '30px', spreed: '-20px', color: '#0003' }]} />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Filter', 'video-gallery')} initialOpen={false}>
						<Typography label={__('Button Typography:', 'video-gallery')} value={filterBtnTypo} onChange={val => setAttributes({ filterBtnTypo: val })} defaults={{ fontSize: { desktop: 14, tablet: 14, mobile: 14 } }} />

						<ColorsControl label={__('Button Colors')} value={filterBtnColors} onChange={val => setAttributes({ filterBtnColors: val })} defaults={{ color: '#fff', bg: '#4527a4' }} />

						<ColorsControl label={__('Button Hover/Active Colors')} value={filterBtnHoverColors} onChange={val => setAttributes({ filterBtnHoverColors: val })} defaults={{ color: '#fff', bg: '#8344c5' }} />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Item', 'video-gallery')} initialOpen={false}>
						<UnitControl label={__('Height:', 'video-gallery')} labelPosition='left' value={itemHeight} onChange={val => setAttributes({ itemHeight: val })} units={[pxUnit(), perUnit(), emUnit()]} />
					</PanelBody>
				</>}
			</>}</TabPanel>
		</InspectorControls>


		<BlockControls>
			<ToolbarGroup className='bPlToolbar'>
				<ToolbarButton label={__('Add New Video', 'video-gallery')} onClick={addVideo}><Dashicon icon='plus' /></ToolbarButton>
			</ToolbarGroup>
		</BlockControls>
	</>;
};
export default Settings;