import { useEffect, useState } from 'react';
import { createRoot } from 'react-dom/client';
import VideoThumbnail from 'react-video-thumbnail';

import { tabController } from '../../Components/utils/functions';

import Settings from './Settings';
import Style from './Style';
import VideoGalleryFilter from './VideoGalleryFilter';

const Edit = props => {
	const { className, attributes, setAttributes, clientId, isSelected } = props;
	const { videos } = attributes;

	useEffect(() => { clientId && setAttributes({ cId: clientId.substring(0, 10) }); }, [clientId]); // Set & Update clientId to cId

	useEffect(() => tabController(), [isSelected]);

	const [activeIndex, setActiveIndex] = useState(0);
	const [itemWidth, setItemWidth] = useState('');

	return <>
		<Settings attributes={attributes} setAttributes={setAttributes} activeIndex={activeIndex} setActiveIndex={setActiveIndex} />

		<div className={className} id={`vgbVideoGallery-${clientId}`}>
			<Style attributes={attributes} clientId={clientId} itemWidth={itemWidth} />

			<div className='vgbVideoGallery'>
				<VideoGalleryFilter attributes={attributes} clientId={clientId} itemWidth={itemWidth} setItemWidth={setItemWidth} />

				<div id={`videoGallery-${clientId}`} className='videoGallery'>
					{videos?.map((item, index) => {
						const { video, poster, albs } = item;

						// const galleryItem = useRef(null);
						const galleryItem = document.querySelector(`#vgbVideoGallery-${clientId} .videoGallery #galleryItem-${index}`);

						// useEffect(() => {
						if (galleryItem) {
							createRoot(galleryItem).render(<></>);

							setTimeout(() => {
								createRoot(galleryItem).render(poster ? <figure className='galleryFigure'>
									<img src={poster} />
								</figure> : <VideoThumbnail width={600} videoUrl={video} snapshotAtTime={2} />);
							}, 0);
						}
						// }, [!videos.length, video, poster]);

						return <div
							key={index}
							onClick={() => setActiveIndex(index)}
							className={`galleryItem ${albs?.map(c => lodash.camelCase(c)).join(' ')} ${index === activeIndex ? 'vgbNowEditing' : ''}`}
							// ref={galleryItem}
							id={`galleryItem-${index}`}
						></div>;
					})}
				</div>
			</div>
		</div>
	</>;
};
export default Edit;